
import "./style/index.scss";
import { Vue, Options, mixins } from "vue-class-component";
import Button from "@/components/button";
import Popup from "../../utils/popup";
import Icon from "@/components/icon";
import { Callback } from "@/types";
@Options({
  name: "YnMessageBox",
  components: {
    Icon,
    Button
  },
  watch: {
    showMsg(val: boolean) {
      if (val) {
        this.open();
      } else {
        this.close();
      }
    }
  }
})
export default class MessageBox extends mixins(Vue, Popup){
  static componentName = "YnMessageBox";

  action = '';
  callback: null | Callback = null;
  showConfirmButton = true;
  showCancelButton = true;
  confirmButtonText = '确定';
  cancelButtonText = '取消';
  message = "";
  title = "";
  beforeClose: Callback | null = null;
  showMsg = false;

  handleClose() {
    this.handAction("close");
  }
  handleConfirm() {
    this.handAction("confirm");
  }
  handleCancel() {
    this.handAction("cancel");
  }
  handleWrapperClick() {
    if (!this.closeOnClickModal) return;
    this.handAction("close");
  }
  handAction(action: string) {
    this.action = action;
    if (typeof this.beforeClose === 'function') {
      this.beforeClose(action, this, this.doClose);
    } else {
      this.doClose();
    }
  }
  doClose() {
    this.showMsg = false;
    if (this.action && this.callback) this.callback(this.action);
  }
  beforeDestroy() {
    if(document.body && this.$el) {
      document.body.removeChild(this.$el);
    }
  }
}
